<template>
  <div class="trip">
    <orderTable :data = "data" v-on:reloadPage="getReloadPage" ></orderTable>
  </div>
</template>

<script>
import orderTable from './orderTable'
import {url, orderUrl} from './url'
import $ from 'jquery'
export default {
  name: 'trip',
  data () {
    return {
      data: []
    }
  },
  created() {
    let url = window.location.href.split('=')[1]
    this.getDataAjax(url).then(e => {
      this.data = e.message
    })
  },
  methods: {
    getReloadPage(data) {
      this.data = data.message.list
    },
    getDataAjax(itemNumber) {
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"get",
      			url: orderUrl+itemNumber,
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
  },
  components: {
    orderTable
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



