<template>
  <div class="plane">
    <el-card>
      <div slot="header" class="clearfix"><span>机票</span></div>
      
      <el-card>
        <div class="card_title"> 
          <!-- 去程 -->
          <h3>去程</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!planeTo.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="planeToInsert"></i>
          
        </div>
        
        <div v-for="(item,i) of planeTo" class="plane_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="planeToDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="planeToInsert"></i>
          <div class="planeTitle">去程日期</div>
          
          <el-date-picker
            v-model="item.to_date"
            type="datetime"
            value-format="timestamp"
            placeholder="选择日期时间">
          </el-date-picker>
          <div class="planeTitle">去程航班号</div>
          <el-input v-model="item.to_number" placeholder="请输入 航班号" class="planeInput"></el-input>
          <div class="planeTitle">去程目的地-出发地</div>
          <el-input v-model="item.to_desc" placeholder="请输入 上海-南极" class="planeInput"></el-input>
          <div class="planeTitle">去程出发时间-到达时间</div>
          <el-input v-model="item.to_start_end" placeholder="请输入 出发时间-到达时间"  class="planeInput"></el-input>
        </div>
      </el-card>
        <!-- 回程 -->
      <el-card style="margin-top:20px;">
        <div class="card_title"> 
          <h3>回程</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!planeBack.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="planeBackInsert"></i>
          
        </div>
        
        <div v-for="(item,i) of planeBack" class="plane_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="planeBackDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="planeBackInsert"></i>
          <div class="planeTitle">回程日期</div>
           <el-date-picker
            v-model="item.back_date"
            type="datetime"
            value-format="timestamp"
            placeholder="选择日期时间">
          </el-date-picker>
          <div class="planeTitle">回程航班号</div>
          <el-input v-model="item.back_number" placeholder="请输入 航班号" class="planeInput"></el-input>
          <div class="planeTitle">回程目的地-出发地</div>
          <el-input v-model="item.back_desc" placeholder="请输入 上海-南极" class="planeInput"></el-input>
          <div class="planeTitle">回程出发时间-到达时间</div>
            <el-input v-model="item.back_start_end" placeholder="请输入 出发时间-到达时间" class="planeInput"></el-input>
        </div>

      </el-card>

      <!-- 联运段 -->
      <el-card style="margin-top:20px;">
        <div class="card_title"> 
          <h3>联运段</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!planeLand.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="planeLandInsert"></i>
          
        </div>
        
        <div v-for="(item,i) of planeLand" class="plane_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="planeLandDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="planeLandInsert"></i>
          <div class="planeTitle">联运段日期</div>
              <el-date-picker
              v-model="item.land_date"
              type="datetime"
              value-format="timestamp"
              placeholder="选择日期时间">
            </el-date-picker>
          <div class="planeTitle">联运段航班号</div>
          <el-input v-model="item.land_number" placeholder="请输入航班号" class="planeInput"></el-input>
          <div class="planeTitle">联运段目的地-出发地</div>
          <el-input v-model="item.land_desc" placeholder="请输入 上海-南极" class="planeInput"></el-input>
          <div class="planeTitle">联运段出发时间-到达时间</div>
          <div style="margin:10px;">{{item.back_start_end}}</div>
            <el-input v-model="item.land_start_end" placeholder="请输入 出发时间-到达时间" class="planeInput"></el-input>
        </div>

      </el-card>
      <el-button type="success" style="float:right; margin:10px;" @click="editFoo">保存</el-button>
    </el-card>
  </div>
</template>

<script>
import url from './url.js'
import $ from 'jquery'
export default {
  name: 'plane_comp',
  data () {
    return {
      plane: {},
      planeTo: [],
      planeLand: [],
      planeBack: [],
      editTripUrl: url.editTripUrl,
    }
  },
  created() {
    let plane = this.$route.params.data.message[0].plane
    this.plane = plane
    
    this.compPlane(plane)
  },
  methods: {
    editFoo() {
      let planeArr = this.planeTo.concat(this.planeLand, this.planeBack)
      let plane = this.$route.params.data.message[0]

      plane.plane = planeArr
      console.log(plane)
      for(let i of planeArr) {
        i.land_start_end_date = null
        i.to_start_end_date = null
        i.back_start_end_date = null
      }
      this.editAjax(JSON.stringify(plane)).then(e => {
        alert(e.message)
      })
    },
    editAjax(data) {
      let url = this.editTripUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url,
            data,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    editPlane() {
      this.editAjax()
    },
    compPlane(plane) {
      for(let i of plane) {

        if(i.to_date_str.length != 0 || i.to_desc.length != 0 || i.to_number.length != 0 ) {
          this.planeTo.push(i)
        }
        if(i.land_date_str.length != 0 || i.land_desc.length != 0 || i.land_number.length != 0 ) {
          this.planeLand.push(i)
        }
        if(i.back_date_str.length != 0 || i.back_desc.length != 0 || i.back_number.length != 0 ) {
          this.planeBack.push(i)
        }
      }
    },
    planeToInsert() {
      this.planeTo.push({
          to_date: '',
          to_number: '',
          to_desc: '',
          to_start_end_date: '',
          land_date: '',
          land_number: '',
          land_desc: '',
          land_start_end_date: '',
          back_date: '',
          back_number: '',
          back_desc: '',
          back_start_end_date: '',
      })
    },
    planeToDel(i) {
      this.planeTo.splice(i, 1)
    },
    planeBackInsert() {
      this.planeBack.push({
          to_date: '',
          to_number: '',
          to_desc: '',
          to_start_end_date: '',
          land_date: '',
          land_number: '',
          land_desc: '',
          land_start_end_date: '',
          back_date: '',
          back_number: '',
          back_desc: '',
          back_start_end_date: '',
      })
    },
    planeBackDel(i) {
      this.planeBack.splice(i, 1)
    },

     planeLandInsert() {
      this.planeLand.push({
          to_date: '',
          to_number: '',
          to_desc: '',
          to_start_end_date: '',
          land_date: '',
          land_number: '',
          land_desc: '',
          land_start_end_date: '',
          back_date: '',
          back_number: '',
          back_desc: '',
          back_start_end_date: '',
      })
    },
    planeLandDel(i) {
      this.planeLand.splice(i, 1)
    },
    // getPlane() {
    //   let plane = this.plane
    //   console.log(this.planeTo)
    //   console.log(this.planeBack)
    //   if(this.planeTo.length) {
    //     plane = plane.concat(this.planeTo)
    //   }
    //   if(this.planeBack.length) {
    //     plane = plane.concat(this.planeBack)
    //   }
    //   if(this.planeLand.length) {
    //     plane = plane.concat(this.planeLand)
    //   }
      
    //   for(let i of plane) {
    //     if(i.to_start_end_date == '') i.to_start_end_date = []
    //     if(i.land_start_end_date == '') i.land_start_end_date = []
    //     if(i.back_start_end_date == '') i.back_start_end_date = []
    //   }
    //   console.log(plane)
    //   this.$emit('getPlane', plane)
    // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.plane{
  margin-top:10px;
}
.plane_box{
  margin: 10px 0;
  padding:10px;
  border:1px solid #ccc;
  border-radius: 10px;
}
.planeTitle{
  margin:10px;
  width:20%;
}
.planeInput{
  width:350px;
}

</style>



