<template>
  <div class="travel_itinerary">
    <el-card>
      <div slot="header" class="clearfix">
        <span>行程单（红色*号的字段为定制师填写） <el-button type="success"  v-show="isExist" @click="downExcel" style=" float:right;">下载</el-button></span>
       
      </div>
      
      <el-card>
        <div class="card_title"> 
          <!-- 去程 -->
          <h3>景点门票</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!ticket.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="ticketInsert"></i>
        </div>
        
        <div v-for="(item,i) of ticket" class="ticket_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="ticketDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="ticketInsert"></i>
          <div class="planeTitle">景点名称<span>*</span></div>
          <div>
            <el-input v-model="item.name" placeholder="请输入景点名称" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">出行人数<span>*</span></div>
          <div>
             <el-input-number v-model="item.pnumber"  :min="1" :max="100" label="描述文字"></el-input-number>
          </div>
          <div class="planeTitle">出行人姓名<span>*</span></div>
          <div>
            <el-input v-model="item.pname" placeholder="请输入出行人姓名" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">人员构成<span>*</span></div>
          <div>
            <el-input v-model="item.ptype" placeholder="( )成人 ( )儿童" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">门票使用日期<span>*</span></div>
          <div>
            <el-date-picker
            v-model="item.pdate"
            
            type="datetime"
            placeholder="选择日期时间">
          </el-date-picker>
          </div>
          <div class="planeTitle">客人门票报价<span>*</span></div>
          <div>
            <el-input v-model="item.pmoney" placeholder="请输入客人门票报价" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">是否预订成功</div>
          <div>
            <el-select v-model="item.success" placeholder="请选择" class="planeInput"> 
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="planeTitle">实际门票价格</div>
          <div>
            <el-input v-model="item.truemoney" placeholder="请输入实际门票价格" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">利润</div>
          <div>
            <el-input v-model="item.profit" placeholder="请输入利润" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">取票方式</div>
          <div>
            <el-input v-model="item.getway" placeholder="请输入取票方式" class="planeInput"></el-input>
          </div>
        </div>
      </el-card>

      <!-- 车票模块 -->
      <el-card>
        <div class="card_title"> 

          <h3>车票</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!bus_ticket.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="bus_ticketInsert"></i>
        </div>
        
        <div v-for="(item,i) of bus_ticket" class="ticket_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="bus_ticketDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="bus_ticketInsert"></i>
          <div class="planeTitle">出发地<span>*</span></div>
          <div>
            <el-input v-model="item.from" placeholder="请输入出发地" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">目的地<span>*</span></div>
          <div>
             <el-input v-model="item.towhere" placeholder="请输入目的地" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">出行人姓名<span>*</span></div>
          <div>
            <el-input v-model="item.pname" placeholder="请输入出行人姓名" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">人员构成<span>*</span></div>
          <div>
            <el-input v-model="item.ptype" placeholder="( )成人 ( )儿童" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">客人用车日期（区间）<span>*</span></div>
          <div>
            <el-input v-model="item.psection" placeholder="请输入用车日期的区间" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">客人取票地点<span>*</span></div>
          <div>
            <el-input v-model="item.getplace" placeholder="请输入取票地点" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">客人取票时间<span>*</span></div>
          <div>
            <el-date-picker
              v-model="item.getdate"
              
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </div>
          <div class="planeTitle">客人门票报价<span>*</span></div>
          <div>
            <el-input v-model="item.pmoney" placeholder="请输入客人门票报价" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">是否预订成功</div>
          <div>        
            <el-select v-model="item.success" placeholder="请选择" class="planeInput"> 
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="planeTitle">实际门票价格</div>
          <div>
            <el-input v-model="item.truemoney" placeholder="请输入实际门票价格" class="planeInput"></el-input>
          </div>
          <div class="planeTitle">利润</div>
          <div>
            <el-input v-model="item.profit" placeholder="请输入利润" class="planeInput"></el-input>
          </div>
        </div>
      </el-card>

      <!-- 包车模块 -->
      <el-card>
        <div class="card_title"> 

          <h3>包车</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!chartered.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="charteredInsert"></i>
        </div>
        
        <div v-for="(item,i) of chartered" class="ticket_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="charteredDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="charteredInsert"></i>
          
          <div class="planeTitle">类型<span>*</span></div>
          <div>        
            <el-select v-model="item.type" placeholder="请选择" class="planeInput"> 
              <el-option
                v-for="item in optionsCar"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          
          <div class="planeTitle">出行人姓名</div>
          <div>
            <el-input v-model="item.pname" placeholder="请输入出行人姓名" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">航班号</div>
          <div>
            <el-input v-model="item.planenumber" placeholder="请输入航班号" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">人员构成</div>
          <div>
            <el-input v-model="item.ptype" placeholder="( )成人 ( )儿童" class="planeInput"></el-input>
          </div>
          
          <div class="planeTitle">行程日期/接机日期</div>
          <div>
              <el-date-picker
              v-model="item.date"
        
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </div>

          <div class="planeTitle">出发时间/送机时间</div>
          <div>
            <el-date-picker
              v-model="item.pdate"
         
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </div>

          <div class="planeTitle">行李数</div>
          <div>
            <el-input-number v-model="item.bagnumber"  :min="1" :max="100" label="描述文字"></el-input-number>
          </div>

          <div class="planeTitle">目的地酒店</div>
          <div>
            <el-input v-model="item.tohotel" placeholder="请填写目的地酒店" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">上车地点</div>
          <div>
            <el-input v-model="item.carplace" placeholder="请输入上车地点" class="planeInput"></el-input>
          </div>
          
          <div class="planeTitle">车型</div>
          <div>
            <el-input v-model="item.caretype" placeholder="请输入车型" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">途中停留城市（市内包车可不填）</div>
          <div>
            <el-input v-model="item.stopplace" placeholder="请输入途中停留城市" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">行程结束城市/目的地</div>
          <div>
            <el-input v-model="item.endplace" placeholder="请输入行程结束城市/目的地" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">结束时间</div>
          <div>
            <el-date-picker
              v-model="item.enddate"
              
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </div>


          <div class="planeTitle">举牌接机</div>
          <div>
            <el-input v-model="item.card" placeholder="请填写是否举牌接机" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">客人报价</div>
          <div>
            <el-input v-model="item.pmoney" placeholder="请输入取票地点" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">实际门票价格</div>
          <div>
            <el-input v-model="item.truemoney" placeholder="请输入实际门票价格" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">利润</div>
          <div>
            <el-input v-model="item.profit" placeholder="请输入利润" class="planeInput"></el-input>
          </div>
        </div>

          
          
      </el-card>
      
      <!-- 其他 -->
      <el-card>
        <div class="card_title"> 
          <!-- 去程 -->
          <h3>其他</h3>
          <i class="el-icon-circle-plus card_icon"   v-show="!schedule.length" style="float: right; cursor:pointer; position:relative; bottom:20px;" @click="scheduleInsert"></i>
        </div>
        
        <div v-for="(item,i) of schedule" class="ticket_box"> 
          <i class="el-icon-delete card_icon"   style="float: right; margin-left:20px; cursor:pointer" @click="scheduleDel(i)"></i>
          <i class="el-icon-circle-plus card_icon"   style="float: right; cursor:pointer" @click="scheduleInsert"></i>
          
          
          <div class="planeTitle">出行人姓名<span>*</span></div>
          <div>
            <el-input v-model="item.name" placeholder="请输入出行人姓名" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">预定项目<span>*</span></div>
          <div>
            <el-input v-model="item.scheduledproject" placeholder="请输入预定项目" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">预定时间<span>*</span></div>
          <div>
            <el-date-picker
              v-model="item.scheduledate"
              
              type="datetime"
              placeholder="选择日期时间">
            </el-date-picker>
          </div>
          
          <div class="planeTitle">客人报价<span>*</span></div>
          <div>
            <el-input v-model="item.pmoney" placeholder="请输入客人报价" class="planeInput"></el-input>
          </div>
          
          <div class="planeTitle">是否预订成功</div>
          <div>
            <el-select v-model="item.success" placeholder="请选择" class="planeInput"> 
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          
          <div class="planeTitle">实际价格</div>
          <div>
            <el-input v-model="item.truemoney" placeholder="请输入实际门票价格" class="planeInput"></el-input>
          </div>

          <div class="planeTitle">利润</div>
          <div>
            <el-input v-model="item.profit" placeholder="请输入利润" class="planeInput"></el-input>
          </div>
          
          
      
        </div>
      </el-card>
      <el-button type="success" @click="saveTravel" style="margin:10px; float:right;">保存</el-button>

    </el-card>
  </div>
</template>

<script>
// import axios from 'axios'
import $ from 'jquery'
import {saveReservoUrl, updateResourceUrl, downItemExcelUrl} from './url'
export default {
  name: 'travel_itinerary',
  data () {
    return {
      ticket: [],
      bus_ticket: [],
      chartered: [],
      schedule: [],

      isExist: false,


      options: [
        {
          value: '成功',
          label: '成功'
        },
        {
          value: '失败',
          label: '失败'
        }
      ],
      optionsCar: [
        {
          value: '包车接机',
          label: '包车接机'
        },
        {
          value: '包车送机',
          label: '包车送机'
        },
        {
          value: '包车全天',
          label: '包车全天'
        },
        {
          value: '包车点对点',
          label: '包车点对点'
        }
      ]
    }
  },
  created() {

  },
  methods: {
    downExcel() {
      window.location.href = downItemExcelUrl+ this.$route.params.mess.id
    },
    error(msg) {
      this.$message.error(msg)
    },
    getData() {
      let id = this.$route.params.mess.id
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"get",
            url: saveReservoUrl + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result)
            },
            error(result) {
              reject(result)
            }
          })
      })
    },
    saveAjax() {
      let ticket = this.ticket 
      let bus_ticket = this.bus_ticket
      let schedule = this.schedule
      let chartered = this.chartered
      // ticket 验证
      for(let item of ticket) {
        if(!(item.name.length && item.pdate && item.pname.length && item.ptype.length )) {
          this.error('请查看景点门票，需填写为红色*号的字段。')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('客人门票报价必须为数字')
          return
        }
      }
      // bus_ticket 验证
      for(let item of bus_ticket) {
        if(!(item.from.length && item.pname.length && item.psection.length && item.ptype.length && item.towhere.length)) {
          this.error('请查看车票，需填写为红色*号的字段。')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('客人门票报价必须为数字')
          return
        }
      }
      // other
      for(let item of schedule) {
        if(!(item.name.length && item.scheduledproject.length)) {
          this.error('请查看车票，需填写为红色*号的字段。')
          return 
        }
        if(!/^[0-9]+$/.test(item.pmoney)) {
          this.error('客人报价必须为数字')
          return
        }
      }

      let data = {
        ticketEntities: this.ticket,
        busTicketEntities: this.bus_ticket,
        charteredEntities: this.chartered,
        scheduleList: this.schedule 
      }
      let id = this.$route.params.mess.id
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            data: JSON.stringify(data),
            url: updateResourceUrl + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result)
            },
            error(result) {
              reject(result)
            }
          })
      })
    },
    saveTravel() {
      let _this = this
      this.saveAjax().then(e => {
        if(e.code === 200) {
          _this.$message.success(e.message)
        }
        else{
          _this.$message.error(e.message)
        }
      })
    },

    // 页面操作
    ticketInsert() {
      this.ticket.push({
        name: '',
        pnumber: '',
        pname: '',
        ptype: '',
        pdate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: '',
        getway: '',
      })
    },
    bus_ticketInsert() {
      this.bus_ticket.push({
        from: '',
        towhere: '',
        pname: '',
        ptype: '',
        psection: '',
        getplace: '',
        getdate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: ''
      })
    },
    charteredInsert() {
      let _this = this
      setTimeout( () => {
        _this.$notify({
          title: '接机送机填写须知',
          message: '须填写客人姓名、接机日期、航班号、人员构成、行李数、目的地酒店、举牌接机、包车报价。',
          duration: 0
        })
      },500)
      setTimeout( () => {
        _this.$notify({
          title: '点对点填写须知',
          message: '须填写客人姓名、行程开始城市、行程日期、出发时间、人员构成、行李数、上车地点、目的地、客人包车报价。',
          duration: 0
        })
      },1000)

      setTimeout( () => {
        _this.$notify({
          title: '全天填写须知',
          message: '须填写客人姓名、上车地点、行程日期、出发时间、人员构成、行李数、选用车型、途中停留城市、结束时间、客人包车报价',
          duration: 0
        })
      },1500)
      
      this.chartered.push({
        type: '',
        pname: '',
        planenumber: '',
        ptype: '',
        date: '',
        pdate: '',
        bagnumber: '',
        tohotel: '',
        card: '',
        pmoney: '',
        truemoney: '',
        profit: '',
        carplace: '',
        caretype: '',
        stopplace: '',
        endplace: '',
        enddate: ''
      })
    },
    scheduleInsert() {
      this.schedule.push({
        name: '',
        scheduledproject: '',
        scheduledate: '',
        pmoney: '',
        success: '',
        truemoney: '',
        profit: ''
      })
    },
    scheduleDel(i) {
      this.schedule.splice(i, 1)
    },
    ticketDel(i) {
      this.ticket.splice(i, 1)
    },
    bus_ticketDel(i) {
      this.bus_ticket.splice(i, 1)
    },
    charteredDel(i) {
      this.chartered.splice(i, 1)
    },
    getUTCTime(str) {
      var str = new Date(str);
      var utc = str.toUTCString();
          return utc;
    }
  },
  created() {

    let _this = this 
    this.getData().then(e => {
      //  ticket: [],
      // bus_ticket: [],
      // chartered: [],
      // schedule: [],
      let data = e.message 
      let isNull = false
      for(let i in data) {
        if(data[i].length) isNull = true
      }
      if(isNull) {
        _this.isExist = true
        let msg = e.message 
        for(let i of msg.busTicketEntities) {
          i.getdate = _this.getUTCTime(i.getdate)
        }
        for(let i of msg.charteredEntities) {
          i.date = _this.getUTCTime(i.date)
          i.enddate = _this.getUTCTime(i.enddate)
          i.pdate = _this.getUTCTime(i.pdate)
        }
        for(let i of msg.scheduleList) {
          i.scheduledate = _this.getUTCTime(i.scheduledate)
        }
        for(let i of msg.ticketEntities) {
          i.pdate = _this.getUTCTime(i.pdate)
        }

        _this.ticket = msg.ticketEntities
        _this.bus_ticket = msg.busTicketEntities
        _this.schedule = msg.scheduleList
        _this.chartered = msg.charteredEntities
      }
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.ticket_box{
  margin: 10px 0;
  padding:10px;
  border-radius: 10px;
  border:1px solid rgba(220, 240, 240, 0.9);
}
.ticket_box>div{
  /* float:left; */
  display: inline-block;
  padding:0;
  margin:0;
  width:23%;
  text-align: center;
  margin-bottom:10px;
}
.ticket_box>div>span{
  color:red;
  position: relative;
  left:20px;
  
}
.ticket_box>div>div{
  width:100%;
  height:100%;
}


</style>



