<template>
  <div class="pay">
   <el-row :gutter="20"  style="margin-top:20px">
      <el-col :span="1"><div class="grid-content bg-purple"></div></el-col>
      <el-col :span="22">
        <div class="grid-content bg-purple">
          <el-button type="primary" class="button_wrap"  :disabled ="!readyPayOrder.length" @click="postAllPayOrderAlert">付款申请</el-button>
        </div>
      </el-col>
      <el-col :span="1"><div class="grid-content bg-purple"></div></el-col>
    </el-row>
    <el-table
    ref="multipleTable"
    :data="orderData_cost"
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="getPickOrder">
    <el-table-column
      type="selection"
      :selectable="getDisabeld"
      width="100">
    </el-table-column>
    <el-table-column
      prop="name"
      label="姓名"
      width="120">
    </el-table-column>
    <el-table-column
      prop="orderNo"
      label="预订平台订单号"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      label="类目"
      show-overflow-tooltip>
      <template slot-scope="scope">
        <i class="el-icon-document"></i>
        <span style="margin-left: 10px">{{ getItemType(scope.row.type) }}</span>
      </template>
    </el-table-column>
    <el-table-column
      prop="category"
      label="类别"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="formatDate"
      label="使用日期"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="total"
      label="金额"
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="status"
      label="付款状态"
      show-overflow-tooltip>
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
import $ from 'jquery'
import {pay_getDataUrl, url} from './url'
export default {
  name: 'pay',
  data () {
    return {
     url: '',
     orderData: [],
     orderData_cost: [],
     current: [],
     readyPayOrder: [],
     allUrl: url
    }
  },
  created() {
    let url = window.location.href.split('=')[1]
    this.getDataAjax(url).then(e => {
      if(e.code === 200) this.orderData_cost = e.message
    })
  },
  methods: {
    getDataAjax(itemNumber) {
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"get",
      			url: pay_getDataUrl+itemNumber,
      			contentType: "application/json; charset=utf-8",
      			dataType: "JSON",
      			success(result) {
      				resolve(result);
      			},
      			error(callback) {
      				reject(callback);
      			}
          })
      })
    },
    getDisabeld(row, index) {
      let status = row.status

      if(status == "未付款" || status == "未付款(已拒绝)") {
        return 1
      }
      else{
        return 0
      }
    },
    getPickOrder(rows) {
      this.readyPayOrder = rows
      console.log(this.readyPayOrder)
    },
    getSelectOrder() {
      let order = this.orderData_cost;
      console.log(order);
    },
    postSelectOrder(){
      let order = this.orderData_cost;
      let items = [];
      let _this = this;
      for(let o of order){
        if(o.checked === true){
          delete o.checked;
          items.push(o);
        }
      }

      $.ajax({
        type:"POST",
        url: this.allUrl+'/applyOrderList',
        data: {
          "orderApplies" : JSON.stringify(items)
        },
        //traditional: true,
        dataType: 'JSON',
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        //contentType: "application/json; charset=utf-8",
        //dataType: "JSON",
        success(result) {
          console.log(result);
          _this.loadUserOrder();
        },
        error(result) {
          console.log(result.message)
        }
      });

    },
    orderStatus(status){
      if(status == 'NEW') return '未成团';
      if(status == 'END_ORDER') return '订单结束'
      if(status == 'FINISH') return '已成团'
    },
    buttondisabled(status){
      if(status != "未付款") return true
    },

    alertButtonClass(index){
      return `#my${index}`;
    },
    alertButtonName(index) {
      return `my${index}`;
    },
    clickOrder(item) {
      this.current.push(item);
    },
    postAllPayOrderAlert() {
      let _this = this
      this.$confirm('您确定申请付款吗？, 付款申请?', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         _this.postAllPayOrder()
         this.$message({
           type: 'success',
           message: '提交成功！!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消申请'
         });
       });
    },
    postAllPayOrder() {
        let data = this.readyPayOrder
        let jsonArr = JSON.stringify(data);
        let _this = this;
      $.ajax({
        type:"POST",
        url: this.allUrl+'/applyOrderList',
        data: {
          "orderApplies" : jsonArr
        },
        //traditional: true,
        dataType: 'JSON',
        headers: {
          'userAgent': "Mozilla/5.0 (Windows NT 6.2; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/60.0.3095.5 Safari/537.36",
          'Access-Control-Allow-Origin': '*'
        },
        //contentType: "application/json; charset=utf-8",
        //dataType: "JSON",
        success(result) {
          console.log(result);
          _this.loadUserOrder();
        },
        error(result) {
          console.log(result.message)
        }
      });
    },
    getItemType(status) {
      if(status == 'hotel') return '酒店';
      if(status == 'flight') return '机票';
      if(status == 'tool') return '旅游用品';
      if(status == 'other') return '其他';
      if(status == 'visa') return '签证';
      if(status == 'car') return '用车';
      if(status == 'flighotel') return '机加酒';
      if(status == 'oneday') return '一日游';
      if(status == 'gongying') return '供应商结算单';
    },
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



