<template>
  <div class="order_trip">
    <!-- card -->

    <el-card>
      <!-- header -->

      <div slot="header" class="clearfix">
        <span>制作行程单</span>
        <!-- <el-button type="primary" style="margin-left:30px;" @click="createNewDay">添加天数</el-button> -->
      </div>

      <!-- body -->
      <el-card v-for="(i, key2) of data" :key="i.id">
        <!-- dayNumber and title -->
          <el-tag>DAY{{i.day}}</el-tag>
          <el-button type="primary"
                     size="small"
                     style="margin-left: 10px;"
                     @click="createDetail(key2)">
                     新增描述
          </el-button>
          <!-- <el-button type="danger"
                     size="small"
                     style="float: right"
                     @click="deteleDay(key2)">
                     删除行程
          </el-button> -->

          <i class="el-icon-delete"   style="float: right; margin-left:20px;cursor:pointer;" @click="deteleDay(key2)"></i>
          <i class="el-icon-circle-plus"   style="float: right; cursor:pointer;" @click="insertDay(i)"></i>
          <el-input v-model="i.title" placeholder="请输入当天标题" style="width:300px; margin-left:10px;"></el-input>
          <el-card>
              <!-- detail -->

              <div v-for="(j, key) of i.itemDescriptions" style="margin-top:20px;">

                <el-input
                  type="textarea"
                  autosize
                  placeholder="请输入内容"
                  style="width:80%"
                  v-model="i.itemDescriptions[key].description">
                </el-input>
                <i class="el-icon-delete deleteIcon"   @click="deleteDetail(key2, key)"></i>
              </div>
              </el-input>
          </el-card>
      </el-card>

      <el-button type="primary" style="float:right;margin-bottom:10px;" @click="editTrip">保存</el-button>

    </el-card>
  </div>
</template>

<script>
import url from './url'
import $ from 'jquery'

export default {
  name: 'order_trip',
  data () {
    return {
      data: [],
      mess: [],
      deleteDetailUrl: url.deleteDetailUrl,
      deleteDayUrl: url.deleteDayUrl,
      deleteTripUrl: url.deleteTripUrl,
      detailUrl: url.detailUrl,
      editTripUrl: url.editTripUrl,
    }
  },
  created() {
    this.getData()
  },
  methods: {
    // reload get detail
    getDetail() {
      let url = this.detailUrl
      let id = this.mess.id
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    // get route data
    getData() {
      if(this.$route.params.data.message[0].length === 0) {
        this.data = [{
          day: 1,
          title: '',
          itemDescriptions: [{
            description: '',
            sort_id: 0
          }]
        }]
      }
      else{
        this.data = this.$route.params.data.message[0].tripItems
      }
      
      this.mess = this.$route.params.mess
      console.log(this.data)
    },
    // save cardMessage to post
    saveCard() {
      let data = this.data
      this.$emit('getCard', data)
    },
    // create a day
    createNewDay() {
      let data = this.data
      let len = data.length+1
      data.push({
        day:len,
        title: '',
        itemDescriptions: [{
          description: ''
        }]
      })
    },
    // add a detail
    createDetail(key) {
      let data = this.data
      data[key].itemDescriptions.push({
        description: ''
      })
    },
    // insert day for addButton
    insertDay(items) {
      let data = this.data
      let day = items.day
      this.data.splice(day, 0, {
        day,
        title: '',
        itemDescriptions: [{
          description: ''
        }]
      })

      for(let i =0; i<data.length; i++) {
        data[i].day = i+1
      }
    },
    // detele a detail
    deleteDetail(key2, key) {
      let data = this.data
      this.$confirm('您要删除此描述吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         // get ajax and splice this description myself
         let item = data[key2].itemDescriptions[key]
         if(item.id == undefined) {
           data[key2].itemDescriptions.splice(key, 1)
         }
         else{
           this.deteleDetailAjax(item.id)
           .then(e => {
             if(e.code === 200) {
              data[key2].itemDescriptions.splice(key, 1)
             }
             else{
               alert(e.message)
             }
           })
         }
         this.$message({
           type: 'success',
           message: '删除成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消删除'
         });
       });


    },
    deteleDetailAjax(id) {
      let url = this.deleteDetailUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    deteleDayAjax(id) {
      let url = this.deleteDayUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    editTrip() {

      let _this = this
      let mess = this.mess
      let data = this.data
      this.$confirm('您要保存此行程吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {

         mess.tripItems = data
         _this.editAjax(JSON.stringify(mess))
         console.log(mess)
         this.$message({
           type: 'success',
           message: '保存成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消保存'
         });
       });

    },
    editAjax(data) {
      let url = this.editTripUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url,
            data,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    deteleDay(key2) {
      let data = this.data
      let id = data[key2].id
      this.$confirm('您要删除当天行程吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
         this.deteleDayAjax(id).then(e => {
           console.log(e)
           if(e.code === 200){
             data.splice(key2, 1)
           }
           else{
             alert(e.message)
           }
         })


         for(let i =0; i<data.length; i++) {
           data[i].day = i+1
         }

         this.$message({
           type: 'success',
           message: '删除成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消删除'
         });
       });
  },

  },
  components: {

  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.deleteIcon{
  line-height: 33px;
  cursor:pointer;
}
</style>



