import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'
import Pay from '@/components/pay'
import Trip from '@/components/trip'
import Order_detail from '@/components/order_trip'
import Order_plane from '@/components/plane_comp'
import TravelItinerary from '@/components/travel_itinerary'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/pay',
      name: 'pay',
      component: Pay
    },
    {
      path: '/trip',
      name: 'trip',
      component: Trip
    },
    {
      path: '/order_detail',
      name: 'order_detail',
      component: Order_detail
    },
    {
      path: '/order_plane',
      name: 'plane_comp',
      component: Order_plane
    },
    {
      path: '/travel_itinerary',
      name: 'travel_itinerary',
      component: TravelItinerary
    }
  ]
})



