<template>
  <div class="orderTable">
    <el-dialog title="收货地址" :visible.sync="messagebox">
      <el-form :model="form">
        <el-form-item label="行程单标题" :label-width="formLabelWidth">
          <el-input v-model="form.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="订单号" :label-width="formLabelWidth">
          <el-input v-model="form.itemNumber" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="出行人姓名" :label-width="formLabelWidth">
          <el-input v-model="form.customer_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="出行人昵称" :label-width="formLabelWidth">
          <el-input v-model="form.customer_wechat" auto-complete="off" laceholder="请填写准确微信昵称(非微信账号)。"></el-input>
        </el-form-item>
        <el-form-item label="出行人手机号" :label-width="formLabelWidth">
          <el-input v-model="form.phoneNumber" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="出发地" :label-width="formLabelWidth">
          <el-input v-model="form.start_point" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="目的地" :label-width="formLabelWidth">
          <el-input v-model="form.end_point" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="出行日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.travel_date"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="归团日期" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.end_date"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="messagebox = false">取 消</el-button>
        <el-button type="primary" @click="editMsg">确 定</el-button>
      </div>
    </el-dialog>
    <el-table
    :data="data"
    style="width: 100%  ">

    <el-table-column
      label="订单号"
      width="180">
      <template slot-scope="scope">
          {{ scope.row.itemNumber }}
      </template>
    </el-table-column>

    <el-table-column
      label="行程单标题"
      width="180">
      <template slot-scope="scope">
          {{ scope.row.title }}
      </template>
    </el-table-column>

    <el-table-column
      label="创建日期"
      width="180">
      <template slot-scope="scope">
          {{ fmtDate(scope.row.create_date) }}
      </template>
    </el-table-column>
    <el-table-column
      label="顾客姓名"
      width="180">
      <template slot-scope="scope">
          {{ scope.row.customer_name}}
      </template>
    </el-table-column>

    <el-table-column
      label="顾客昵称"
      width="180">
      <template slot-scope="scope">
          {{ scope.row.customer_wechat }}
      </template>
    </el-table-column>

    <el-table-column
      label="出团日期"
      width="180">
      <template slot-scope="scope">
          {{ fmtDate(scope.row.travel_date) }}
      </template>
    </el-table-column>

    <el-table-column
      label="归团日期"
      width="180">
      <template slot-scope="scope">
          {{ fmtDate(scope.row.end_date) }}
      </template>
    </el-table-column>

    
    <el-table-column label="操作" width="500">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">
            行程
        </el-button>
        <el-button
          size="mini"
          @click="getOrdered(scope.$index, scope.row)">
            行程预定单
        </el-button>
        <el-button
          size="mini"
          @click="getPlane(scope.$index, scope.row)">
            机票
        </el-button>
        <el-button
          size="mini"
          @click="getMessage(scope.$index, scope.row)">
            编辑信息
        </el-button>
        <el-button
          size="mini"
          type="success"
          @click="downExcel(scope.$index, scope.row)">
            下载
        </el-button>
        <el-button
          size="mini"
          type="danger"
          @click="deleteTrip(scope.$index, scope.row)">
            删除
        </el-button>
      </template>
    </el-table-column>
  </el-table>




  </div>
</template>

<script>
import url from './url'
import $ from 'jquery'
export default {
  name: 'order',
  data () {
    return {
      detailUrl: url.detailUrl,
      deleteTripUrl: url.deleteTripUrl,
      orderUrl: url.orderUrl,
      editMsgUrl: url.editMsgUrl,
      downExcelUrl: url.downExcelUrl,
      deleteData: [],

      form: {
        title: ''
      },
      messagebox: false,
      formLabelWidth: "120px"
    }
  },
  created() {
  },
  methods: {
    downExcel(index, row) {
      window.location.href = this.downExcelUrl+row.id
    },
    getPlane(index, row) {
      let _this = this
      let id = row.id
      this.getDetail(id).then(e => {
        _this.$router.push({
          name: 'plane_comp',
          params: {
            data: e
          }
        })
      })
    },
    editMsg() {

      let _this = this
      this.$confirm('您要保存此信息吗？', '提示', {
         confirmButtonText: '确定',
         cancelButtonText: '取消',
         type: 'warning'
       }).then(() => {
        let message = ''
         _this.editAjax().then(e => {
           message = e.message
         })
         _this.messagebox = false

         this.$message({
           type: 'success',
           message: '保存成功!'
         });
       }).catch(() => {
         this.$message({
           type: 'info',
           message: '已取消保存'
         });
       });

    },
    editAjax() {
      let url = this.editMsgUrl
      let data = JSON.stringify(this.form)
      console.log(data)
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"POST",
            url,
            data,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    getMessage(index, row) {
      this.form = row
      this.form.travel_date = this.fmtDate(this.form.travel_date)
      this.form.end_date = this.fmtDate(this.form.end_date)
      this.messagebox = true
      console.log(row)
    },
    deteleTripAjax(id) {
      let url = this.deleteTripUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result);
            },
            error(callback) {
              reject(callback);
            }
          })
      })
    },
    fmtDate(obj){
      let _this = this
      const date =  new Date(obj);
      const y = 1900+date.getYear();
      const m = "0"+(date.getMonth()+1);
      const d = "0"+date.getDate();
      return y+"-"+m.substring(m.length-2,m.length)+"-"+d.substring(d.length-2,d.length);
    },
    deleteTrip(index, row) {
      let _this = this
      let id = row.id


      this.$confirm('您将删除此行程单, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deteleTripAjax(id).then(e => {
            console.log(e)
            location.reload()
          })

          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    },
    getOrdered(index, row) {
      let _this = this 
      this.getDetail(row.id)
      .then(e => {
        _this.$router.push({
          name: 'travel_itinerary',
          params: {
            data: e,
            mess: row
          }
        })
      })
    },
    handleEdit(index, row) {
      let _this = this
      this.getDetail(row.id)
      .then(e => {
        console.log(e)
        _this.$router.push({
          name: 'order_detail',
          params: {
            data: e,
            mess: row
          }
        })
      })
    },
    getDetail(id) {
      let url = this.detailUrl
      return new Promise( (resolve, reject) =>{
          $.ajax({
            type:"GET",
            url: url + id,
            crossDomain: true,
            xhrFields:{withCredentials:true},
            // contentType: "application/json; charset=utf-8",
            dataType: "JSON",
            success(result) {
              resolve(result)
            },
            error(callback) {
              reject(callback)
            }
          })
      })
    }
  },
  props: {
    data: {
      type: Array,
      default: () => []
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.out{
  float: right;
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 20%;
}
.el-form-item{
  text-align: center;
}

</style>



